<template>
    <div>
        <div class="topTitle">
            <div class="leftTitle">Ck</div>
        </div>
        <div class="mainMseeage">

            <el-form :inline="true" :model="searchForm" class="searchForm">
                <el-form-item label="ck">
                    <el-input v-model="searchForm.ck" placeholder="ck" clearable></el-input>
                </el-form-item>
                <!-- <el-form-item label="ptPin">
                    <el-input v-model="searchForm.ptPin" placeholder="ptPin" clearable></el-input>
                </el-form-item> -->
                <el-form-item label="">
                    <el-input v-model="searchForm.qlName" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="searchForm.status" placeholder="" clearable>
                        <el-option v-for="item in statusList" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit"></el-button>
                </el-form-item>
            </el-form>

            <el-table :data="tableData" style="width: 100%" v-loading="tableLoading">
                <el-table-column prop="ck" label="ck" width="" align="center">
                </el-table-column>
                <!-- <el-table-column prop="ptPin" label="ptPin" width="100" align="center">
                </el-table-column> -->
                <el-table-column prop="remark" label="" width="" align="center">
                </el-table-column>
                <el-table-column prop="" label="" width="100" align="center">
                    <template slot-scope="scoped">
                        {{scoped.row.status | statusType}}
                    </template>
                </el-table-column>
                <el-table-column prop="qlRemark" label="" width="100" align="center">
                </el-table-column>
                <el-table-column prop="qlindex" label="" width="100" align="center">
                </el-table-column>
                <el-table-column label="" fixed="right" width="200" align="center">
                    <template slot-scope="scoped">
                        <el-button type="text" :loading="scoped.row.openLoading" v-if="scoped.row.status=='1'"
                            @click="changeCK(1,scoped.row)">
                        </el-button>
                        <el-button type="text" :loading="scoped.row.closeLoading" v-if="scoped.row.status=='0'"
                            @click="changeCK(2,scoped.row)">
                        </el-button>
                        <el-button type="text" :loading="scoped.row.deleteLoading" @click="changeCK(3,scoped.row)">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNo"
                :page-sizes="[10, 20, 30]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="tableTotal">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { Message } from 'element-ui'
import { getJdCkList, disableJdCk, enableJdCk, deleteJdCk } from '../axiosRequest/allRequest'
export default {
    filters: {
        statusType(value) {
            const obj = {
                0: '',
                1: '',
            }
            return obj[value]
        }
    },
    data() {
        return {
            searchForm: {
                ck: '',
                ptPin: "",
                status: '',
                qlName: ''
            },
            tableData: [],
            tableTotal: 0,
            pageNo: 1,
            pageSize: 10,
            statusList: [
                {
                    value: '0',
                    label: ''
                },
                {
                    value: '1',
                    label: ''
                }
            ],
            tableLoading: false
        }
    },

    created() {
        this.onSubmit()
    },
    methods: {
        onSubmit() {
            let param = {
                pageSize: this.pageSize,
                pageNo: this.pageNo,
                ...JSON.parse(JSON.stringify(this.searchForm))
            }
            this.tableLoading = true
            getJdCkList(param).then((res) => {
                this.tableLoading = false
                if (res.code == 200) {
                    let a = res.data.jdCkList
                    a.forEach((item, index) => {
                        this.$set(item, 'deleteLoading', false)
                        this.$set(item, 'openLoading', false)
                        this.$set(item, 'closeLoading', false)
                    })
                    this.tableData = a
                    this.tableTotal = res.data.total

                } else {
                    Message.error(res.message)
                }
            })
        },
        handleSizeChange(val) {
            this.pageSize = val
            this.onSubmit()
        },
        handleCurrentChange(val) {
            this.pageNo = val
            this.onSubmit()
        },
        changeCK(type, row) {
            this.$confirm(`${type == '1' ? '' : (type == '2' ? '' : '')}`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let param = [{
                    qlId: row.qlId,
                    id: row.id
                }]
                if (type == 1) {
                    this.tableData.forEach((item, index) => {
                        if (item.id == row.id) {
                            this.$set(item, 'openLoading', true)
                        }
                    })
                    //
                    enableJdCk(param).then((res) => {
                        this.tableData.forEach((item, index) => {
                            if (item.id == row.id) {
                                this.$set(item, 'openLoading', false)
                            }
                        })
                        if (res.code == 200) {
                            Message.success('')
                            this.onSubmit()
                        } else {
                            Message.error(res.message)
                        }
                    })
                } else if (type == 2) {
                    //
                    this.tableData.forEach((item, index) => {
                        if (item.id == row.id) {
                            this.$set(item, 'closeLoading', true)
                        }
                    })
                    disableJdCk(param).then((res) => {
                        this.tableData.forEach((item, index) => {
                            if (item.id == row.id) {
                                this.$set(item, 'closeLoading', false)
                            }
                        })
                        if (res.code == 200) {
                            Message.success('')
                            this.onSubmit()
                        } else {
                            Message.error(res.message)
                        }
                    })
                } else {
                    //
                    this.tableData.forEach((item, index) => {
                        if (item.id == row.id) {
                            this.$set(item, 'deleteLoading', true)
                        }
                    })
                    deleteJdCk(param).then((res) => {
                        this.tableData.forEach((item, index) => {
                            if (item.id == row.id) {
                                this.$set(item, 'deleteLoading', false)
                            }
                        })
                        if (res.code == 200) {
                            Message.success('')
                            this.onSubmit()
                        } else {
                            Message.error(res.message)
                        }
                    })
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>

</style>