import { request } from './request'
import qs from "qs";

//
export function login(data) {
    return request({
        method: 'post',
        url: '/robot/token/login',
        data: data
    })
}

//
export function checkLogin() {
    return request({
        method: 'get',
        url: '/robot/token/check',
    })
}

//
export function check(data) {
    return request({
        method: 'post',
        url: '/robot/token/register',
        data: data
    })
}

export let getJdInfo = (params) => {
    return request({
        methods: 'get',
        url: `/robot/jdHelp/getJdInfo`,
    })
}

