<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle"></div>
      <div class="rightButton">
        <el-input placeholder="//" v-model="searchEnv" class="input-with-select">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <el-button type="primary" size="mini" @click="newEnvVisible = true"></el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <div class="ChooseAll" v-if="ShowChooseAll">
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <el-button type="primary" @click="batch()"></el-button>
        <span style="margin-left: 10px"><span style="color: #409eff">{{ choosedNum }}</span></span>
      </div>
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%"
        @selection-change="handleSelectionChange" :height="tableHeight">
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column type="index" width="55" label="">
        </el-table-column>
        <el-table-column prop="" label="" width="" align="center">
        </el-table-column>
        <el-table-column prop="taskName" label="" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="taskName" label="" width="" align="center">
        </el-table-column>
        <el-table-column prop="taskName" label="" width="" align="center">
        </el-table-column>

        <el-table-column prop="taskName" label="" width="" align="center">
          <template slot-scope="scoped">
            <el-tag type="danger"></el-tag>
            <el-tag type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="" label="" width="" fixed="right" align="center">
          <template slot-scope="scoped">
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button type="text" class="operationButton" @click="editEnv(scoped.row)"><i class="el-icon-edit"
                  style="color: #409eff"></i>
              </el-button>
            </el-tooltip>

            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button type="text" class="operationButton" @click="startEnv(scoped.row)"><i
                  class="el-icon-circle-check" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button type="text" class="operationButton" @click="forbiddenEnv(scoped.row)"><i
                  class="el-icon-remove-outline" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>

            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button type="text" class="operationButton" @click="deleteEnv(scoped.row)"><i class="el-icon-delete"
                  style="color: #409eff"></i>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="changePage"
        :page-sizes="[10, 20, 30]" :page-size="changeSize" layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal">
      </el-pagination>
    </div>

    <!--  -->
    <!-- label-position="top" -->
    <el-dialog title="" :visible.sync="newEnvVisible" width="30%" @close="handleClose('newEnvForm')">
      <el-form :model="newEnvForm" :rules="newTaskrules" ref="newEnvForm" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="newEnvForm.name" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="autoSplit">
          <el-tooltip class="item" effect="dark" content="" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>

          <el-radio-group v-model="newEnvForm.autoSplit">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="value">
          <el-input v-model="newEnvForm.value" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="newEnvForm.remark" placeholder="" clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="newEnvVisible = false"> </el-button>
        <el-button type="primary" @click="addEnvTask('newEnvForm')"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editEnvVisible" width="30%" @close="editEnvClose('editEnvForm')">
      <el-form :model="editEnvForm" :rules="editEnvRules" ref="editEnvForm" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="editEnvForm.name" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="value">
          <el-input v-model="editEnvForm.value" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="editEnvForm.remark" placeholder="" clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editEnvVisible = false"> </el-button>
        <el-button type="primary" @click="sureEditEnv('editEnvForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { Message } from "element-ui";
export default {
  data() {
    return {
      searchEnv: "",
      ShowChooseAll: false,
      tableHeight: window.innerHeight - 120,
      changePage: 1,
      changeSize: 10,
      tableTotal: 0,
      choosedNum: 0,
      newEnvVisible: false,
      editEnvVisible: false,
      tableData: [
        {
          taskName: "1",
        },
        {
          taskName: "2",
        },
        {
          taskName: "2",
        },
      ],
      newEnvForm: {
        name: "",
        autoSplit: 1,
        value: "",
        remark: "",
      },
      editEnvForm: {
        name: "",
        value: "",
        remark: "",
      },
      newTaskrules: {
        name: [
          { required: true, message: "", trigger: "blur" },
        ],
        autoSplit: [],
        value: [
          { required: true, message: "", trigger: "blur" },
        ],
        remark: [],
      },
      editEnvRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
        ],
        value: [
          { required: true, message: "", trigger: "blur" },
        ],
        remark: [],
      },
    };
  },
  created() { },
  mounted() { },
  methods: {
    handleSelectionChange(val) {
      this.choosedNum = val.length;
      if (val.length > 0) {
        this.ShowChooseAll = true;
      } else {
        this.ShowChooseAll = false;
      }
    },
    handleSizeChange(val) {
      //
    },
    handleCurrentChange(val) {
      //
    },
    handleClose(formName) {
      this.$refs[formName].resetFields();
    },
    editEnvClose(formName) {
      this.$refs[formName].resetFields();
    },
    addEnvTask(val) {
      this.$refs[val].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    editEnv(row) {
      //
      this.editEnvVisible = true;
    },
    sureEditEnv(val) {
      this.$refs[val].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    startEnv(row) {
      //
      this.$confirm("xxx?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        Message.success("111");
      });
    },
    forbiddenEnv(row) {
      //
      this.$confirm("xxx?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        Message.success("222");
      });
    },
    deleteEnv(row) {
      //
      this.$confirm("xxx?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        Message.success("333");
      });
    },
  },
};
</script>

<style lang="less" scoped>
.el-radio-group {
  width: 100%;
}
</style>