<template>
  <div id="backgroundImages">
    <div class="jdtopTitle"></div>
    <div class="mainBox">
      <div class="leftM">
        <div class="echartsBox" id="echartsBox"></div>
        <div class="ckBox">
          <h3>   </h3>
          <div class="maxMsg">
            <div>ck:{{ jdInfoList.allCkCount }} </div>
            <div>:{{ jdInfoList.jdfruit }} &nbsp;&nbsp;&nbsp;&nbsp; <el-button type="text" style="color:#0000ff"
                @click="getList('fruit')">
                
              </el-button>
            </div>
            <div>:{{ jdInfoList.jdpet }} &nbsp;&nbsp;&nbsp;&nbsp; <el-button type="text" style="color:#0000ff"
                @click="getList('pet')">
              </el-button>
            </div>
            <div>:{{ jdInfoList.jdbean }} &nbsp;&nbsp;&nbsp;&nbsp; <el-button type="text" style="color:#0000ff"
                @click="getList('plant')">
              </el-button>
            </div>
            <div>:{{ jdInfoList.jdddfactory }} </div>
            <div>:{{ jdInfoList.jdhealth }}</div>
            <div>:{{ jdInfoList.jdjxfactory }}</div>
            <div>:{{ jdInfoList.jdsgmh }}</div>
          </div>
        </div>
      </div>
      <div class="rightM">
        <div class="rightTop">
          <div style="font-size: 20px; color: blue; padding-bottom: 5px">
            
          </div>
          <h2></h2>
          <br />
          <h2 style="text-align: left"></h2>
          <h3 style="text-align: left">
            &nbsp;&nbsp;&nbsp;&nbsp;38
          </h3>
          <h2 style="text-align: left"></h2>
          <h3 style="text-align: left">
            &nbsp;&nbsp;&nbsp;&nbsp;55
          </h3>
          <h2 style="text-align: left"></h2>
          <h3 style="text-align: left">
            &nbsp;&nbsp;&nbsp;&nbsp;39
          </h3>
        </div>
        <div class="rightCenter">
          <div class="subBox">
            <span class="subMax"></span>
            <el-input class="ckInput" v-model="form.ck" placeholder="ck"></el-input>
            <div style="text-align: center; margin: 10px">
              <el-button type="primary" :loading="submitLoading" @click="onSubmit"></el-button>
              <div style="font-size: 15px; color: red; margin-top: 20px">
                tip:
              </div>
            </div>
          </div>
        </div>
        <div class="rightBottom">
          <el-button type="primary" @click="jumpJD"></el-button>
        </div>
      </div>
    </div>

    <el-dialog title="" :visible.sync="helpVisible" v-loading="helpListLoading" width="30%" @close="helpClose">
      <el-empty description="" v-if="listData.length==0"></el-empty>
      <div v-else>
        <div v-for="(item,index) in listData" :key="index">{{item}}</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="helpVisible = false"> </el-button>
      </span>
    </el-dialog>


  </div>
</template>

<script>
import { Message } from "element-ui";
import { addCk, getJdInfo, getHelpList } from "../axiosRequest/allRequest";
import * as echarts from "echarts";
export default {
  data() {
    return {
      helpVisible: false,
      helpListLoading: false,
      listData: [],
      form: {
        ck: "",
      },
      jdInfoList: {},
      submitLoading: false,
      option: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
          },
        },
        backgroundColor: "rgba(128,128,128,0.5)",

        legend: {
          data: [""],
        },
        xAxis: {
          type: "category",
          data: [],
          axisLine: {
            show: true,
            lineStyle: {
              color: "black",
            },
          },
        },
        yAxis: {
          type: "value",
          axisLine: {
            show: true,
            lineStyle: {
              color: "black",
            },
          },
        },
        series: [
          {
            data: [],
            type: "line",
            name: "",
            showBackground: true,
            backgroundStyle: {
              color: "rgba(180, 180, 180, 0.2)",
            },
            itemStyle: {
              normal: {
                label: {
                  show: true, //
                  position: "top", //
                  textStyle: {
                    //
                    color: "black",
                    fontSize: 16,
                  },
                },
              },
            },
          },
        ],
      },
    };
  },

  created() {
    this.getInfo();
  },

  mounted() { },

  methods: {
    onSubmit() {
      this.submitLoading = true;
      addCk({ ck: this.form.ck, level: 2 }).then((res) => {
        console.log(res, 'res');
        this.submitLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          Message.success("");
          this.form.ck = "";
        }
      });
    },
    jumpJD() {
      if (this.jdInfoList.jdLoginUrl) {
        window.open(this.jdInfoList.jdLoginUrl);
      } else {
        Message.warning("");
      }
    },
    getInfo() {
      getJdInfo().then((res) => {
        if (res.code == 200) {
          let a = JSON.parse(JSON.stringify(res.data));
          this.jdInfoList = a;
          this.option.series[0].data = [];
          if (a.jdIncome) {
            for (let i in a.jdIncome) {
              this.option.xAxis.data.push(i);
              this.option.series[0].data.push(a.jdIncome[i]);
            }
          } else {
            this.option.series[0].data = [0, 0, 0, 0, 0, 0, 0];
          }
          var myChart = echarts.init(document.getElementById("echartsBox"));
          myChart.setOption(this.option);
          window.addEventListener("resize", () => {
            if (myChart) {
              myChart.resize();
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },

    getList(type) {
      this.helpVisible = true
      this.helpListLoading = true
      getHelpList({ type: type }).then((res) => {
        this.helpListLoading = false
        if (res.code == 200) {
          this.listData = res.data
        } else {
          Message.error(res.message)
        }
      })
    },

    helpClose() {
      this.listData = []
    }
  },
};
</script>

<style scope lang="less">
#backgroundImages {
  min-height: 600px;
  width: 100%;
  height: 100vh;
  background: url("https://i.imgtg.com/2022/07/24/r1VMD.png");
  background-size: 100% 100%;
  overflow: hidden;
}

.el-input__inner {
  background: rgba(255, 255, 255, 0.1) !important;
}

.jdtopTitle {
  margin: 20px 0px;
  color: blue;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
}

@media screen and (min-width: 900px) {
  .mainBox {
    height: 90vh;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;

    .leftM {
      flex: 8;
      overflow: hidden;

      .echartsBox {
        padding-right: 30%;
        height: 40vh;
      }

      .ckBox {
        text-align: center;
        margin-right: 30%;
        margin-top: 20px;
        padding: 10px;
        height: 40vh;
        overflow-y: auto;
        color: blue;
        font-size: calc(100vw * 25 / 1920);

        &::-webkit-scrollbar {
          background-color: gray;
          border-radius: 10px;
        }

        &::-webkit-scrollbar-thumb {
          background-color: rgb(77, 76, 76);
          border-radius: 10px;
        }

        &::-webkit-scrollbar-button {
          background-color: rgb(77, 76, 76);
          border-radius: 10px;
        }
      }
    }

    .rightM {
      flex: 2;
      height: 90vh;
      padding: 0 20px;
      display: flex;
      flex-direction: column;
      text-align: center;

      h2 {
        font-size: 1.15em;
      }

      h3 {
        font-size: 0.8em;
      }

      .rightTop {
        height: 45vh;
        white-space: normal;
        word-break: break-all;
        overflow: hidden;
        background-color: rgba(221, 220, 235, 0.5);
        margin-bottom: 5px;
        border-radius: 5px;
      }

      .rightCenter {
        height: 38vh;
        margin-bottom: 0.3125rem;

        display: flex;
        align-items: center;

        .subBox {
          background-color: rgba(221, 220, 235, 0.5);
          margin: auto;
          width: 100%;
          border-radius: 5px;
          padding: 30px 0px;

          .subMax {
            color: blue;
            font-size: calc(100vw * 30 / 1920);
          }
        }
      }

      .rightBottom {
        height: 10vh;
        // background-color: rgba(221, 220, 235, 0.5);
        border-radius: 5px;
        display: flex;
        align-items: center;

        .el-button {
          margin: auto;
          // font-size: 30px;
          font-size: calc(100vw * 30 / 1920);
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .mainBox {
    .leftM {
      .echartsBox {
        height: 200px;
      }

      .ckBox {
        margin: 10px 0;
        color: blue;

        .maxMsg {
          font-size: 16px;
        }
      }
    }

    .rightM {
      .rightTop {
        background-color: rgba(221, 220, 235, 0.5);
        border-radius: 5px;
        padding: 2px;
      }

      .rightCenter {
        .subBox {
          padding: 10px 0;
          background-color: rgba(221, 220, 235, 0.5);
          margin: 10px 0;

          .subMax {
            color: blue;
            font-size: 30px;
          }
        }
      }

      .rightBottom {
        margin-bottom: 10px;
      }
    }
  }
}
</style>