<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle"></div>
      <div class="rightButton">
        <!-- <el-input v-model="searchForm.url" placeholder="url" clearable></el-input> -->
        <el-button type="primary" size="mini" @click="addCk(1)">
          ck</el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <el-form :inline="true" :model="formInline" class="searchForm">

        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="ck">
              <el-input v-model="formInline.ck" placeholder="ck" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="ptPin">
              <el-input v-model="formInline.ptPin" placeholder="ptPin" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="level">
              <el-select v-model="formInline.level" placeholder="level" clearable>
                <el-option v-for="item in levelOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="status">
              <el-select v-model="formInline.status" placeholder="status" clearable>
                <el-option v-for="item in statusOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="remark">
              <el-input v-model="formInline.remark" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item>
              <el-button type="primary" @click="onSubmit"></el-button>
            </el-form-item>
          </el-col>
        </el-row>

      </el-form>

      <el-table :data="tableData" v-loading="tableLoading" style="width: 100%" border>
        <el-table-column prop="ptPin" label="ptPin" width="200" align="center">
        </el-table-column>
        <el-table-column prop="remark" label="" width="" align="center">
        </el-table-column>
        <el-table-column label="level" width="100" align="center">
          <template slot-scope="scoped">
            {{ scoped.row.level | level }}
          </template>
        </el-table-column>
        <el-table-column prop="expiryTime" label="" width="200" align="center">
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scoped">
            {{ scoped.row.status | status }}
          </template>
        </el-table-column>
        <el-table-column prop="jd" label="" width="120" align="center">
        </el-table-column>
        <el-table-column label="" align="center" width="120" fixed="right">
          <template slot-scope="scoped">
            <el-button type="text" @click="addCk(2, scoped.row)"></el-button>
            <el-button type="text" :loading="scoped.row.delLoading" @click="deleteCK(scoped.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNo"
        :page-sizes="[10, 20, 30]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal">
      </el-pagination>
    </div>

    <!-- ck -->
    <el-dialog :title="ckName" :visible.sync="addVisible" ref="ckvis" width="30%" @close="addClose('addForm')">
      <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="100px">
        <el-form-item label="ck" prop="ck">
          <el-input v-model="addForm.ck" placeholder="ck" clearable></el-input>
        </el-form-item>
        <el-form-item label="level" prop="level">
          <el-select v-model="addForm.level" placeholder="level" clearable>
            <el-option v-for="item in levelOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="addForm.remark" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" v-if="addForm.level=='1'" prop="expiryTime">
          <el-date-picker v-model="addForm.expiryTime" type="date" placeholder="" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false"> </el-button>
        <el-button type="primary" :loading="sureLoading" @click="sureAdd('addForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  addCk,
  getJdCks,
  updateJdCk,
  deleteJdCks,
} from "../axiosRequest/allRequest";

export default {
  filters: {
    level(value) {
      const obj = {
        0: "",
        1: "vip",
        2: "",
        3: "",
      };
      return obj[value];
    },
    status(value) {
      const obj = {
        0: "",
        1: "",
      };
      return obj[value];
    },
  },
  data() {
    return {
      formInline: {},
      tableData: [],
      ckName: "CK",
      addVisible: false,
      addForm: {
        ck: "",
        remark: "",
        level: "",
        expiryTime: "",
        id: "",
      },
      addRules: {
        ck: [{ required: true, message: "ck", trigger: "blur" }],
        remark: [],
        level: [{ required: true, message: "level", trigger: "blur" }],
        expiryTime: [],
        id: [],
      },
      levelOptions: [
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "vip",
        },
        {
          value: 2,
          label: "",
        },
        {
          value: 3,
          label: "",
        },
      ],
      statusOptions: [
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "",
        },
      ],
      sureLoading: false,
      tableLoading: false,
      pageNo: 1,
      pageSize: 10,
      tableTotal: 0
    };
  },

  created() {
    this.onSubmit();
  },
  methods: {
    addCk(i, row) {
      this.addVisible = true;
      if (i == 1) {
        this.ckName = "CK";
      } else {
        this.ckName = "CK";
      }

      if (row) {
        this.$nextTick(() => {
          this.addForm.ck = row.ck;
          this.addForm.remark = row.remark;
          this.addForm.level = row.level;
          this.addForm.expiryTime = row.expiryTime;
          this.addForm.id = row.id;
        })

      }
    },

    sureAdd(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true;
          let param = this.addForm
          if (this.addForm.level != 1) {
            delete param.expiryTime
            // delete param.id
          }
          if (this.ckName == "CK") {
            addCk(param).then((res) => {
              this.sureLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success("");
                this.addVisible = false;
                this.onSubmit();
              }
            });
          } else {
            updateJdCk(param).then((res) => {
              this.sureLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success("");
                this.addVisible = false;
                this.onSubmit();
              }
            });
          }
        } else {
          return false;
        }
      });
    },

    addClose(formName) {
      this.$refs[formName].resetFields();
    },

    onSubmit() {
      this.tableLoading = true
      let param = {
        ...JSON.parse(JSON.stringify(this.formInline)),
        pageNo: this.pageNo,
        pageSize: this.pageSize
      }
      getJdCks(param).then((res) => {
        this.tableLoading = false
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          let a = JSON.parse(JSON.stringify(res.data.jdCkList));
          a.forEach((item, index) => {
            this.$set(item, "delLoading", false);
          });
          this.tableData = a;
          this.tableTotal = res.data.total
        }
      });
    },

    handleSizeChange(val) {
      this.pageSize = val
      this.onSubmit()
    },
    handleCurrentChange(val) {
      this.pageNo = val
      this.onSubmit()
    },

    deleteCK(row) {
      this.$confirm("CK", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        this.tableData.forEach((item) => {
          if (row.id == item.id) {
            this.$set(item, "delLoading", true);
          }
        });
        deleteJdCks([row.id]).then((res) => {
          this.tableData.forEach((item) => {
            if (row.id == item.id) {
              this.$set(item, "delLoading", false);
            }
          });
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success("");
            this.onSubmit();
          }
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
::v-deep .searchForm {
  .el-form-item__label {
    width: 60px;
  }
}
</style>