<template>
    <div>
        <div class="topTitle">
            <div class="leftTitle"></div>
            <div class="rightButton">
                <el-button type="primary" size="mini" @click="addCk(1)">
                    </el-button>
            </div>
        </div>

        <div class="mainMseeage">
            <el-form :inline="true" :model="formInline" class="searchForm">
                <el-form-item label="command">
                    <el-input v-model="formInline.command" placeholder="command" clearable></el-input>
                </el-form-item>
                <el-form-item label="function">
                    <el-input v-model="formInline.function" placeholder="function" clearable></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit"></el-button>
                </el-form-item>
            </el-form>

            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="command" label="" width="" align="center">
                </el-table-column>
                <el-table-column prop="function" label="" width="" align="center">
                </el-table-column>
                <el-table-column prop="reply" label="" align="center"></el-table-column>
                <el-table-column prop="isBuiltIn" label="" align="center">
                    <template slot-scope="scoped">
                        {{scoped.row.isBuiltIn | isBuiltInType}}
                    </template>
                </el-table-column>
                <el-table-column prop="createdTime" label="" align="center"></el-table-column>
                <el-table-column prop="updatedTime" label="" align="center"></el-table-column>
                <el-table-column label="" fixed="right" align="center">
                    <template slot-scope="scoped">
                        <el-button type="text" :disabled="scoped.row.isBuiltIn==1" @click="addCk(2,scoped.row)">
                        </el-button>
                        <el-button type="text" :disabled="scoped.row.isBuiltIn==1" :loading="scoped.row.deleteLoading"
                            @click="deleteCd(scoped.row)">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>


        <!-- / -->
        <el-dialog :title="boxName" :visible.sync="addVisible" width="30%" @close="addClosed('addForm')">
            <el-form :model="addForm" :rules="addrules" ref="addForm">
                <el-form-item label="" prop="command">
                    <el-input v-model="addForm.command" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="function">
                    <el-input v-model="addForm.function" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="reply">
                    <el-input v-model="addForm.reply" clearable></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addVisible = false"> </el-button>
                <el-button type="primary" :loading="sureLoading" @click="sureAdd('addForm')"> </el-button>
            </span>
        </el-dialog>


    </div>
</template>

<script>
import { Message } from 'element-ui'
import { onScopeDispose } from 'vue'
import { queryCommand, addCommand, updateCommand, deleteCommand } from '../axiosRequest/allRequest'
export default {

    filters: {
        isBuiltInType(value) {
            const obj = {
                0: '',
                1: ''
            }
            return obj[value]
        }
    },

    data() {
        return {
            formInline: {},
            tableData: [],
            addVisible: false,
            boxName: '',
            diType: '1',
            addForm: {
                command: '',
                function: '',
                reply: '',
                id: ''
            },
            addrules: {
                command: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                function: [],
                reply: []
            },
            sureLoading: false
        }
    },

    created() {
        this.onSubmit()
    },

    methods: {
        onSubmit() {
            queryCommand(this.formInline).then((res) => {
                if (res.code == 200) {
                    let a = JSON.parse(JSON.stringify(res.data))
                    a.forEach((item, index) => {
                        this.$set(item, 'deleteLoading', false)
                    })
                    this.tableData = a
                } else {
                    Message.error(res.message)
                }
            })
        },

        addCk(type, row) {
            this.diType = type
            if (type == '1') {
                this.boxName = ''
            } else {
                this.boxName = ''
                this.addForm.command = row.command
                this.addForm.function = row.function
                this.addForm.reply = row.reply
                this.addForm.id = row.id
            }

            this.addVisible = true
        },

        sureAdd(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = this.addForm
                    this.sureLoading = true
                    if (this.diType == '1') {
                        //
                        delete param.id
                        addCommand(param).then((res) => {
                            this.sureLoading = false
                            if (res.code == 200) {
                                Message.success('')
                                this.addVisible = false
                                this.onSubmit()
                            } else {
                                Message.error(res.message)
                            }
                        })
                    } else {
                        //
                        updateCommand(param).then((res) => {
                            this.sureLoading = false
                            if (res.code == 200) {
                                Message.success('')
                                this.addVisible = false
                                this.onSubmit()
                            } else {
                                Message.error(res.message)
                            }
                        })
                    }


                } else {
                    return false;
                }
            });
        },

        addClosed(formName) {
            this.$refs[formName].resetFields();
        },
        deleteCd(row) {
            this.$confirm(`${row.command}`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.tableData.forEach((item, index) => {
                    if (item.id == row.id) {
                        this.$set(item, 'deleteLoading', true)
                    }
                })
                deleteCommand([row.id]).then((res) => {
                    if (res.code == 200) {
                        Message.success('')
                        this.onSubmit()
                    } else {
                        Message.error(res.message)
                    }
                })
            })
        }
    }
}
</script>

<style lang="less" scoped>

</style>