<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">
        {{  scriptTitle ? scriptTitle : ""  }}
      </div>
      <div class="rightButton" v-if="editorEdit">
        <el-tooltip class="item" effect="dark" content="" placement="bottom">
          <el-button type="primary" size="mini" @click="addNewScript"><i class="el-icon-circle-plus-outline"></i>
          </el-button>
        </el-tooltip>

        <el-tooltip class="item" effect="dark" content="" placement="bottom">
          <el-button type="primary" size="mini" :disabled="scriptTitle ? false : true" @click="editScript"><i
              class="el-icon-edit"></i>
          </el-button>
        </el-tooltip>

        <el-tooltip class="item" effect="dark" content="" placement="bottom">
          <el-button type="primary" size="mini" :disabled="scriptTitle ? false : true" @click="deleteScript"><i
              class="el-icon-delete"></i></el-button>
        </el-tooltip>
        <el-button type="primary" size="mini" @click="debugScript"></el-button>
      </div>

      <div class="rightButton" v-else>
        <el-button type="primary" size="mini" :loading="saveEditLoading" @click="saveEdit"></el-button>
        <el-button type="primary" size="mini" @click="returnEdit"></el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <div class="leftMeun">
        <el-input placeholder="" v-model="searchScript" class="input-with-select">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <el-tree :data="MeunData" :props="defaultProps" :default-expand-all=false @node-click="handleNodeClick">
        </el-tree>
      </div>
      <div class="rightCode">
        <prism-editor class="my-editor height-300" :readonly="editorEdit" v-model="code" :highlight="highlighter"
          :line-numbers="lineNumbers">
        </prism-editor>
      </div>
    </div>

    <el-dialog title="" :visible.sync="addNewVisible" width="30%" @close="addNewClose('addNewForm')">
      <el-form :model="addNewForm" :rules="addNewRule" ref="addNewForm" class="demo-addNewForm">
        <el-form-item label="" prop="fileNname">
          <el-input v-model="addNewForm.fileNname" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="ParentDirectory">
          <el-select v-model="addNewForm.ParentDirectory" placeholder="" clearable>
            <el-option v-for="item in ParentDirectoryList" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addNewVisible = false"> </el-button>
        <el-button type="primary" @click="sureAdd('addNewForm')"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-drawer :with-header="false" :visible.sync="debugging" direction="rtl">
      <div class="buttonBox">
        <el-button type="primary"></el-button>
        <el-button type="primary"></el-button>
        <el-button type="primary"></el-button>
        <el-button type="primary"></el-button>
        <el-button type="primary" @click="outDebug"></el-button>
      </div>
    </el-drawer>
  </div>
</template>
<script>
import { PrismEditor } from "vue-prism-editor";
import "vue-prism-editor/dist/prismeditor.min.css"; // import the styles somewhere

// import highlighting library (you can use any library you want just return html string)
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-clike";
import "prismjs/components/prism-javascript";
import "prismjs/themes/prism-tomorrow.css"; // import syntax highlighting styles

export default {
  components: {
    PrismEditor,
  },
  data() {
    return {
      editorEdit: true,
      addNewVisible: false,
      saveEditLoading: false,
      scriptTitle: "",
      searchScript: "",
      debugging: false,
      defaultProps: {
        children: "children",
        label: "label",
      },
      MeunData: [
        {
          label: " 1",
          children: [
            {
              label: " 1-1",
              children: [
                {
                  label: " 1-1-1",
                },
              ],
            },
          ],
        },
        {
          label: " 2",
          children: [
            {
              label: " 2-1",
              children: [
                {
                  label: " 2-1-1",
                },
              ],
            },
            {
              label: " 2-2",
              children: [
                {
                  label: " 2-2-1",
                },
              ],
            },
          ],
        },
        {
          label: " 3",
          children: [
            {
              label: " 3-1",
              children: [
                {
                  label: " 3-1-1",
                },
              ],
            },
            {
              label: " 3-2",
              children: [
                {
                  label: " 3-2-1",
                },
              ],
            },
          ],
        },
      ],
      code: 'console.log("Hello World")',
      lineNumbers: true,
      addNewForm: {
        fileNname: '',
        ParentDirectory: ''
      },
      addNewRule: {
        fileNname: [
          { required: true, message: '', trigger: 'blur' }
        ],
        ParentDirectory: []
      },
      ParentDirectoryList: [
        { value: '1', label: '1' },
        { value: '2', label: '2' },
        { value: '3', label: '3' },
      ]
    };
  },
  created() { },
  mounted() { },
  methods: {
    highlighter(code) {
      return highlight(code, languages.js); //returns html
    },
    handleNodeClick(val, e) {
      //tree
      if (e.level == 3) {
        this.scriptTitle = e.label
        //code
      }
    },
    addNewScript() {
      this.addNewVisible = true
    },
    sureAdd(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          return false;
        }
      });
    },
    addNewClose(formName) {
      this.$refs[formName].resetFields();
    },
    editScript() {
      this.editorEdit = false
      //code
    },
    returnEdit() {
      this.editorEdit = true
      //code
    },
    saveEdit() {
      //    
      this.saveEditLoading = true
      setTimeout(() => {
        this.saveEditLoading = false
      }, 2000)
    },
    deleteScript() {
      this.$confirm(`${this.scriptTitle}?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
      })
    },
    debugScript() {
      this.debugging = true
    },
    outDebug() {
      this.debugging = false
    }
  },
};
</script>

<style lang="less" scoped>
.mainMseeage {
  display: flex;

  .leftMeun {
    padding: 10px;
    width: 20%;
    height: 100%;
    background-color: white;
    overflow-y: auto;
  }

  .rightCode {
    width: 80%;
    height: 100%;
    background-color: aquamarine;
  }
}

::v-deep .rtl {
  width: 100% !important;
}

.buttonBox {
  padding: 10px;
  border-bottom: 1px solid gray;
}

.my-editor {
  background: #2d2d2d;
  color: #ccc;
  font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  padding: 5px;
}

/* optional */
.prism-editor__textarea:focus {
  outline: none;
}

/* not required: */
.height-300 {
  height: 100%;
}
</style>