<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle"></div>
    </div>

    <div class="mainMseeage">
      <el-card>
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="" name="1">
            <div
              style="border-bottom: 0.5px solid #f0f0f0; padding-bottom: 10px"
            >
              
            </div>
            <el-form
              :model="loginForm"
              :rules="loginRules"
              ref="loginForm"
              class="loginForm"
            >
              <el-form-item label="" prop="account">
                <el-input
                  v-model="loginForm.account"
                  clearable
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="password">
                <el-input
                  v-model="loginForm.password"
                  clearable
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="saveLogin('loginForm')"
                  ></el-button
                >
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-table
              v-if="activeName == 2"
              :data="applySettingsData"
              style="width: 100%"
              stripe
            >
              <el-table-column
                prop="name"
                label=""
                width="200"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="ClientID"
                label="Client ID"
                width="200"
                align="center"
              >
                <template slot-scope="scoped">
                  {{ scoped.row.ClientID
                  }}<i
                    class="el-icon-document-copy copyIcon"
                    @click="copyMesg(scoped.row.ClientID)"
                  ></i>
                </template>
              </el-table-column>
              <el-table-column
                prop="ClientSecret"
                label="Client Secret"
                width="200"
                align="center"
              >
                <template slot-scope="scoped">
                  *******<i
                    class="el-icon-document-copy copyIcon"
                    @click="copyMesg(scoped.row.ClientSecret)"
                  ></i>
                </template>
              </el-table-column>
              <el-table-column prop="jurisdiction" label="" align="center">
                <template slot-scope="scoped">
                  <el-tag
                    class="jurisdictionTag"
                    v-for="(item, index) in scoped.row.jurisdiction"
                    :key="index"
                    type="info"
                  >
                    {{ item.a }}</el-tag
                  >
                </template>
              </el-table-column>
              <el-table-column prop="" label="" width="200" align="center">
                <template slot-scope="scoped">
                  <el-tooltip effect="dark" content="" placement="top">
                    <i
                      class="el-icon-edit operationi"
                      @click="editOperation(scoped.row)"
                    ></i>
                  </el-tooltip>

                  <el-tooltip effect="dark" content="reset" placement="top">
                    <i
                      class="el-icon-refresh-right operationi"
                      @click="resetOperation(scoped.row)"
                    ></i>
                  </el-tooltip>

                  <el-tooltip effect="dark" content="" placement="top">
                    <i
                      class="el-icon-delete operationi"
                      @click="deleteOperation(scoped.row)"
                    ></i>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-form
              :model="NotificationForm"
              :rules="NotificationRules"
              ref="NotificationForm"
              class="loginForm"
            >
              <el-form-item label="" prop="notificationMode">
                <el-select
                  v-model="NotificationForm.notificationMode"
                  clearable
                  placeholder=""
                >
                  <el-option
                    v-for="item in notificationList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button
                  type="primary"
                  @click="saveNotification('NotificationForm')"
                  ></el-button
                >
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <el-table
              v-if="activeName == 4"
              :data="loginLogData"
              stripe
              style="width: 100%"
            >
              <el-table-column type="index" label="" width="80">
              </el-table-column>
              <el-table-column label="" width="" align="center">
                <template slot-scope="scoped">
                  {{ scoped.row.loginDate | TimeconversionSs }}
                </template>
              </el-table-column>
              <el-table-column prop="" label="" width="" align="center">
              </el-table-column>
              <el-table-column prop="" label="IP" align="center">
              </el-table-column>
              <el-table-column prop="" label="" align="center">
              </el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scoped">
                  <el-tag v-if="scoped.row.LoginStatus == 2" type="danger"
                    ></el-tag
                  >
                  <el-tag v-else type="success"></el-tag>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-card>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editOperationVisible"
      width="30%"
      @close="editOperationClose('editOperationForm')"
    >
      <el-form
        :model="editOperationForm"
        :rules="editOperationRules"
        ref="editOperationForm"
      >
        <el-form-item label="" prop="name">
          <el-input
            v-model="editOperationForm.name"
            clearable
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="jurisdiction">
          <el-select
            v-model="editOperationForm.jurisdiction"
            clearable
            multiple
            placeholder=""
          >
            <el-option
              v-for="item in jurisdictionList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editOperationVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="sureEditOperation('editOperationForm')"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: "1",
      loginForm: {
        account: "",
        password: "",
      },
      loginRules: {
        account: [
          { required: true, message: "", trigger: "blur" },
          // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
      },
      //
      applySettingsData: [
        {
          name: "1212",
          ClientID: "123123",
          ClientSecret: "ClientSecret123123",
          jurisdiction: [
            { a: "" },
            { a: "" },
            { a: "" },
          ],
        },
        {
          name: "1223",
          ClientID: "122322",
          ClientSecret: "ClientSecret122333",
          jurisdiction: [
            { a: "" },
            { a: "" },
            { a: "" },
          ],
        },
        {
          name: "1234",
          ClientID: "123422",
          ClientSecret: "ClientSecret123444",
          jurisdiction: [
            { a: "" },
            { a: "" },
            { a: "" },
          ],
        },
      ],
      //
      editOperationVisible: false,
      editOperationForm: {
        name: "",
        jurisdiction: [],
      },
      editOperationRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        jurisdiction: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      jurisdictionList: [
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
        { value: "4", label: "" },
        { value: "5", label: "" },
        { value: "6", label: "" },
        { value: "7", label: "" },
      ],
      //
      NotificationForm: {
        notificationMode: "",
      },
      NotificationRules: {
        notificationMode: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      notificationList: [{ value: "1", label: "1" }],
      //
      loginLogData: [
        {
          loginDate: 1661932187066,
          LoginStatus: 1,
        },
        {
          loginDate: 1661932189999,
          LoginStatus: 2,
        },
      ],
    };
  },
  created() {},
  mounted() {
    let a = new Date().getTime();
    console.log(a, "aaa");
  },
  methods: {
    handleClick() {},
    saveLogin(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    sureEditOperation(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    saveNotification(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    //
    editOperation(row) {
      this.editOperationVisible = true;
    },
    editOperationClose(formName) {
      this.$refs[formName].resetFields();
    },
    //
    resetOperation(row) {
      const h = this.$createElement;
      this.$msgbox({
        title: "",
        message: h("p", null, [
          h("div", null, [
            h("span", null, ""),
            h("span", { style: "color: #fbad60" }, row.name),
            h("span", null, "Secret?"),
          ]),
          h("div", null, [
            h(
              "div",
              { style: "color: #8c8c8c" },
              `${row.name}token`
            ),
          ]),
        ]),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "",
        cancelButtonText: "",
        center: true,
      }).then(() => {
        //
        this.$message({
          type: "info",
          message: "action: ",
        });
      });
    },
    deleteOperation(row) {
      const h = this.$createElement;
      this.$msgbox({
        title: "",
        message: h("p", null, [
          h("div", null, [
            h("span", null, ""),
            h("span", { style: "color: #fbad60" }, row.name),
            h("span", null, "?"),
          ]),
        ]),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "",
        cancelButtonText: "",
        center: true,
      }).then(() => {
        //
        this.$message({
          type: "info",
          message: "action: ",
        });
      });
    },
    copyMesg(item) {
      //  
      var cInput = document.createElement("input");
      cInput.value = item;
      document.body.appendChild(cInput);
      cInput.select(); // 

      document.execCommand("copy");

      this.$message({
        type: "success",
        message: "",
      });
      //  
      document.body.removeChild(cInput);
    },

    changeThemColor(type){
        console.log(type);
    }
  },
};
</script>

<style lang="less" scoped>
.loginForm {
  width: 30%;
}

.copyIcon {
  color: #1890ff;
  margin-left: 5px;
  cursor: pointer;
}

.jurisdictionTag {
  margin-right: 5px;
}

.operationi {
  margin-right: 10px;
  color: #1890ff;
  cursor: pointer;
  font-size: 20px;
}
</style>